"use client";

import { useState } from "react";
import { DashboardWrapper } from "@/components/layout/dashboard-wrapper";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Badge } from "@/components/ui/badge";
import { ScheduleSessionModal } from "@/components/scheduling/schedule-session-modal";
import { CalendarPlus, User, TrendingUp, Star, BookOpen, ArrowLeft, MessageSquare } from "lucide-react";
import { useParams, useRouter } from "next/navigation"; // To get studentId and navigate

// Mock Student Data Interface (can be shared or imported)
interface Student {
  id: string;
  name: string;
  avatar: string;
  email: string;
  subjects: string[];
  progress?: number;
  lastSession?: Date;
  // Add more detailed fields if needed
  attendanceRate?: number;
  performance?: { subject: string; score: number }[];
  sessionHistory?: { date: string; duration: number; topic: string }[];
}

// Mock Data (same as list page, potentially fetch details here)
const mockStudents: Student[] = [
  {
    id: "student1",
    name: "John Doe",
    avatar: "/avatars/john-doe.png",
    email: "john.doe@example.com",
    subjects: ["Calculus", "Physics"],
    progress: 85,
    lastSession: new Date(Date.now() - 3 * 24 * 60 * 60 * 1000),
    attendanceRate: 95,
    performance: [{ subject: "Calculus", score: 92 }, { subject: "Physics", score: 88 }],
    sessionHistory: [{ date: "2024-04-10", duration: 60, topic: "Derivatives" }, { date: "2024-04-05", duration: 60, topic: "Kinematics" }],
  },
  {
    id: "student2",
    name: "Jane Smith",
    avatar: "/avatars/jane-smith.png",
    email: "jane.smith@example.com",
    subjects: ["Physics", "Chemistry"],
    progress: 78,
    lastSession: new Date(Date.now() - 5 * 24 * 60 * 60 * 1000),
    attendanceRate: 100,
    performance: [{ subject: "Physics", score: 85 }, { subject: "Chemistry", score: 80 }],
    sessionHistory: [{ date: "2024-04-08", duration: 60, topic: "Thermodynamics" }],
  },
  {
    id: "student3",
    name: "Alice Brown",
    avatar: "/avatars/alice-brown.png",
    email: "alice.brown@example.com",
    subjects: ["Chemistry"],
    progress: 92,
    lastSession: new Date(Date.now() - 1 * 24 * 60 * 60 * 1000),
    attendanceRate: 98,
    performance: [{ subject: "Chemistry", score: 95 }],
    sessionHistory: [{ date: "2024-04-12", duration: 45, topic: "Stoichiometry" }],
  },
];

export default function StudentProfilePage() {
  const params = useParams();
  const router = useRouter();
  const studentId = params.studentId as string;

  const [isScheduleModalOpen, setIsScheduleModalOpen] = useState(false);

  // Find the student (replace with actual data fetching)
  const student = mockStudents.find(s => s.id === studentId);

  if (!student) {
    // Handle student not found
    return (
      <DashboardWrapper>
        <div className="container mx-auto py-8">
            <Button variant="outline" onClick={() => router.back()} className="mb-4">
               <ArrowLeft className="mr-2 h-4 w-4" /> Back to Students
            </Button>
            <p>Student not found.</p>
        </div>
      </DashboardWrapper>
    );
  }

  const handleScheduleClick = () => {
    setIsScheduleModalOpen(true);
  };

  return (
    <DashboardWrapper>
      <div className="container mx-auto py-8 space-y-6">
         {/* Back Button & Header */}
         <div>
            <Button variant="outline" size="sm" onClick={() => router.back()} className="mb-4">
               <ArrowLeft className="mr-2 h-4 w-4" /> Back to Students
            </Button>
            <div className="flex items-center gap-4">
               <Avatar className="h-16 w-16 border">
                  <AvatarImage src={student.avatar} alt={student.name} />
                  <AvatarFallback className="text-xl">{student.name.split(' ').map(n => n[0]).join('')}</AvatarFallback>
               </Avatar>
               <div>
                  <h1 className="text-3xl font-bold">{student.name}</h1>
                  <p className="text-muted-foreground">{student.email}</p>
               </div>
            </div>
         </div>

         {/* Main Content Grid */}
         <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
             {/* Left Column: Key Stats & Actions */}
             <div className="md:col-span-1 space-y-4">
                <Card>
                    <CardHeader>
                        <CardTitle>Quick Stats</CardTitle>
                    </CardHeader>
                    <CardContent className="space-y-3 text-sm">
                        {student.progress && (
                           <div className="flex justify-between items-center">
                              <span className="text-muted-foreground inline-flex items-center gap-1.5"><TrendingUp size={16}/> Progress</span>
                              <span className="font-medium">{student.progress}%</span>
                           </div>
                        )}
                         {student.attendanceRate && (
                           <div className="flex justify-between items-center">
                              <span className="text-muted-foreground">Attendance</span>
                              <span className="font-medium">{student.attendanceRate}%</span>
                           </div>
                        )}
                         {student.lastSession && (
                           <div className="flex justify-between items-center">
                              <span className="text-muted-foreground">Last Session</span>
                              <span className="font-medium">{student.lastSession.toLocaleDateString()}</span>
                           </div>
                        )}
                    </CardContent>
                </Card>
                 <Card>
                    <CardHeader>
                        <CardTitle>Subjects</CardTitle>
                    </CardHeader>
                    <CardContent className="flex flex-wrap gap-2">
                       {student.subjects.map((subject) => (
                          <Badge key={subject} variant="secondary">{subject}</Badge>
                       ))}
                    </CardContent>
                </Card>
                <Button onClick={handleScheduleClick} className="w-full">
                    <CalendarPlus className="mr-2 h-4 w-4" /> Schedule Session
                </Button>
                 <Button variant="outline" className="w-full">
                    <MessageSquare className="mr-2 h-4 w-4" /> Send Message
                 </Button>
             </div>

             {/* Right Column: Performance & History */}
             <div className="md:col-span-2 space-y-6">
                {student.performance && student.performance.length > 0 && (
                     <Card>
                        <CardHeader>
                           <CardTitle>Recent Performance</CardTitle>
                           <CardDescription>Latest scores or assessments.</CardDescription>
                        </CardHeader>
                        <CardContent>
                           <ul className="divide-y divide-border">
                              {student.performance.map((perf, index) => (
                                 <li key={index} className="py-2 flex justify-between items-center">
                                    <span className="font-medium text-sm">{perf.subject}</span>
                                    <span className="text-sm text-muted-foreground">Score: {perf.score}%</span>
                                 </li>
                              ))}
                           </ul>
                        </CardContent>
                     </Card>
                  )}

                 {student.sessionHistory && student.sessionHistory.length > 0 && (
                    <Card>
                        <CardHeader>
                           <CardTitle>Session History</CardTitle>
                        </CardHeader>
                        <CardContent>
                           <ul className="divide-y divide-border">
                              {student.sessionHistory.map((session, index) => (
                                 <li key={index} className="py-2 flex justify-between items-center">
                                    <div>
                                       <span className="font-medium text-sm">{session.topic}</span>
                                       <p className="text-xs text-muted-foreground">{session.date}</p>
                                    </div>
                                    <span className="text-sm text-muted-foreground">{session.duration} mins</span>
                                 </li>
                              ))}
                           </ul>
                        </CardContent>
                     </Card>
                  )}
             </div>
         </div>

      </div>

      {/* Scheduling Modal */}
      <ScheduleSessionModal
        isOpen={isScheduleModalOpen}
        onClose={() => setIsScheduleModalOpen(false)}
        role="TUTOR"
        availableUsers={student ? [student] : []} // Pass only the current student
      />
    </DashboardWrapper>
  );
} 